import React, { useState, useEffect } from "react";
import {
  // Pie chart for desktop
  PieChart,
  Pie,
  Cell,
  PieProps,
  // Bar chart for mobile
  BarChart,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  Bar,
} from "recharts"; // node_modules\@types\recharts\index.d.ts
import { useWindowSize } from "hooks";
import { SELECT_ARRAYS, TRANSACTION_COUNT } from "core/constants";
import { centsToDecimal } from "core/utils";
import { Loading } from "components";
import { fetchRules } from "services/rules";
import { fetchTransactions } from "services/transactions";
import { getExpensesByCategoryGraphData } from "process/charts";
import { getCategoryColour } from "core/colours";
import cn from "classnames";
import s from "./PercentageChartPage.module.css";

import testdata from "testdata/expensesByCategoryGraphData.json";

const { CATEGORIES } = SELECT_ARRAYS;

type CategoryAndCent = {
  name: string;
  value: number;
  label: string;
};
const categoriesAndCentsDefault: CategoryAndCent[] = [];

type Props = {
  isBoxed?: boolean;
  cloud: boolean;
  onBoxClick?: (target: string) => void;
};

// -------------------------------------
// COMPONENT:
// https://recharts.org/en-US/api/PieChart
const PercentageChartPage = ({
  isBoxed = false,
  cloud,
  onBoxClick = () => {},
}: Props) => {
  // HOOKS:
  const { desktop, mobile, width } = useWindowSize();

  const isFullPage = !isBoxed;
  const title = "Percentage: expenses per category (€/mo)";

  const getFactors = (): number[] => {
    if (isBoxed) return [0.61, 2, 1.75, 1.7];
    if (mobile) return [0.42, 0.5, 1.75, 1.7];
    return [1, 1, 1, 1];
  };

  const [factor, outerRadiusFactor, heightFactor, cyFactor] = getFactors();

  // STATE:
  const [categoriesAndCents, setCategoriesAndCents] = useState(
    categoriesAndCentsDefault
  );
  const [total, setTotal] = useState(0);
  const [from, setFrom] = useState("");
  const [to, setTo] = useState("");
  const [dayCount, setDayCount] = useState(0);
  const [totalPerMonth, setTotalPerMonth] = useState(0);

  const getData = async () => {
    if (cloud) {
      return testdata;
    } else {
      const r = await fetchRules(cloud);
      const t = await fetchTransactions(0, TRANSACTION_COUNT, cloud);
      const data = getExpensesByCategoryGraphData(t, r);
      return data;
    }
  };

  // EFFECTS - GET DATA:
  useEffect(() => {
    const fetchData = async () => {
      const {
        graphDataItems,
        total,
        from,
        to,
        dayCount,
        totalPerMonth,
      } = await getData();

      setCategoriesAndCents(graphDataItems);
      setTotal(total);
      setFrom(from);
      setTo(to);
      setDayCount(dayCount);
      setTotalPerMonth(totalPerMonth);
    };
    fetchData();
    return () => {}; // on unmount
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [cloud]);

  // FUNCTIONS:
  type PieProps2 = { index: number };
  const renderCustomizedLabel = (props: PieProps) => {
    const { index } = (props as unknown) as PieProps2;
    return categoriesAndCents[index].label;
  };

  if (isFullPage && (categoriesAndCents.length === 0 || !from || !to)) {
    return <Loading />;
  }

  const barChartData = mobile
    ? categoriesAndCents
        .slice(categoriesAndCents.length - 20)
        .map(x => ({
          ...x,
          name: x.name.replace(" ", "-"),
          value: (x.value / total / 100).toFixed(3),
        }))
        .reverse()
    : [];

  return (
    <div
      className={cn("page", s.root)}
      onClick={isFullPage ? () => {} : () => onBoxClick("/percentage")}
    >
      {isFullPage ? <h3>{title}</h3> : <h4>{title}</h4>}

      {desktop && (
        <PieChart width={800 * factor} height={450 * factor * heightFactor}>
          <Pie
            data={categoriesAndCents}
            cx={400 * factor}
            cy={225 * factor * cyFactor}
            labelLine={false}
            label={isFullPage ? renderCustomizedLabel : () => ""}
            outerRadius={180 * factor * outerRadiusFactor}
            fill="#8884d8"
            dataKey="value"
            isAnimationActive={false}
            // blendStroke={true}
          >
            {categoriesAndCents.map(item => (
              <Cell
                key={item.name}
                fill={getCategoryColour(CATEGORIES, item.name)}
              />
            ))}
          </Pie>
        </PieChart>
      )}

      {mobile && (
        <BarChart
          width={width ? width - 30 : 800}
          height={600}
          layout="vertical"
          data={barChartData}
          maxBarSize={100}
          margin={{ left: 50 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis type="number" />
          <YAxis dataKey="name" type="category" />
          <Tooltip />
          {/* <Legend /> */}
          <Bar dataKey="value" /* unit="%" */ fill="#8884d8" />
          {/* <Bar dataKey="uv" fill="#82ca9d" /> */}
        </BarChart>
      )}

      {isFullPage && (
        <>
          <div>
            {from} - {to}
          </div>
          <div>€{totalPerMonth} a month</div>
          <div>
            €{total} in {dayCount} days
          </div>

          {desktop && (
            <>
              <br />
              <table className={s.categoryTable}>
                <thead>
                  <tr>
                    <th>Category</th>
                    <th>Month</th>
                    <th>%</th>
                    <th>Period</th>
                  </tr>
                </thead>
                <tbody>
                  {[...categoriesAndCents].reverse().map((item, index) => (
                    <tr key={`${item.name}_${index}`}>
                      <td>{item.name}</td>

                      {item.label ? (
                        item.label
                          .replace(item.name, "")
                          .replace("(", "")
                          .replace(")", "")
                          .replace("%", "")
                          .split(" ")
                          .filter(Boolean) // delete empty
                          .map((x, index) => <td key={index}>{x}</td>)
                      ) : (
                        <>
                          <td key="a"></td>
                          <td key="b"></td>
                        </>
                      )}

                      <td>{centsToDecimal(item.value.toString())}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </>
          )}
        </>
      )}
    </div>
  );
};

export default PercentageChartPage;
